@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Balsamiq Sans', cursive;  
}

#root {
  background-color:#0049B7;       
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  width: 50vw;
  height: 65vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container h1{
  color:white;
  font-size: 3vw;
  background: linear-gradient(30deg,#ff1d58,#f75990,#ffe604);
  -webkit-text-stroke-width: 1.4px;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  text-transform: uppercase;
}
.container textarea {
  width: 90%;
  height: 70%;
  margin: 20px;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  border-radius: 10px;
  outline: none;
  padding: 15px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.685);
  font-weight: light;
}
.count{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 40px ;
  color: white;
  background: linear-gradient(90deg,#ff1d58,#f75990,#ffe604);
  font-weight: bold;
  border-radius: 5px;
  font-family: 'Balsamiq Sans', cursive;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.685);
}
@media only screen and (max-width: 1024px) and (min-width:750px){
  
  .container {
      width: 80vw;
      height: 40vh;
      
  }
  .container h1{
    font-size: 5vw;
  }
}
@media only screen and (max-width: 550px) and (min-width:320px){
  .container {
      width: 80vw;
      height: 60vh;   
  }
  .container h1{
  -webkit-text-stroke-width: 0.5px;
      font-size: 7vw;

  }
}
